<template>
  <div>
    <!-- 图片库 -->
    <div class="box">
      <div class="flex-row-start">
        <el-row style="width:100%;" :gutter="20">
          <!-- 左侧操作区域 -->
          <el-col :span="6">
            <div class="materinalOperation">
              <el-card class="box-card">
                <div class="search" style="margin-bottom:20px;">
                  <el-input placeholder="搜索分组" v-model="getGalleryForm.galleryName" class="input-with-select searchInput">
                    <el-button @click="getGaller" slot="append" icon="el-icon-search"></el-button>
                  </el-input>
                </div>
                <div class="class flex-column-center">
                  <div class="addClass">
                    <el-button style="width:200px;" plain @click="insertGalleryShow = true">添加分组</el-button>
                  </div>
                  <div class="classList" :style="{height: OperationHeight + 'px'}" :height="OperationHeight + 'px'">
                    <ul class="infinite-list" style="overflow:auto;height:100%;">
                      <li v-for="tag in imageClassTags" :key="tag.name" @click="activeClass(tag)" class="li infinite-list-item">
                        <el-tag style="width:200px;" :type="tag.type" v-if="tag.id == 0 || tag.id == -1">{{tag.galleryName }} {{tag.id === -1 ? '' : '( ' + tag.totalAmount + ' )' }}</el-tag>
                        <el-popover  v-else placement="right" trigger="hover">
                          <el-button @click.stop="editClass(tag)" type="text" icon="el-icon-edit">编辑</el-button>
                          <el-button @click.stop="deleteClass(tag)" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
                          <el-tag style="width:200px;" :type="tag.type" slot="reference">{{tag.galleryName }} ( {{tag.totalAmount}} )</el-tag>
                        </el-popover>
                      </li>
                    </ul>
                  </div>
                </div>
              </el-card>
            </div>
          </el-col>
          <!-- 右侧图片展示区域 -->
          <el-col :span="18">
            <div class="materinalContent">
              <el-card class="box-card" style="margin-bottom:20px">
                <h3 style="margin-bottom:20px;">{{activeClassName}}</h3>
                <div class="flex-row-between">
                  <el-button type="primary" @click="uploadImg=true">上传图片</el-button>
                  <div class="search">
                    <el-input placeholder="搜索图片" v-model="getImgsForm.imageName" class="input-with-select searchInput">
                      <el-button @click="getImgs" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                  </div>
                </div>
              </el-card>
              <el-card class="box-card">
                <div v-if="imgs.length > 0">
                  <div style="line-height:24px;"><el-checkbox @change="allSelect" v-model="allChecked">图片全选</el-checkbox></div>
                  <div class="classList" :style="{height: ImageBoxHeight + 'px'}">
                    <ul class="infinite-list flex-row-start1 ul" style="overflow-x: hidden;overflow-y:auto;height:100%;">
                      <li v-for="(item,index) in imgs" :key="index" class="infinite-list-item item">
                        <div class="demo-image__preview">
                          <el-image class="curImg" @click="showBigImg(item.imageUrl)" style="width: 200px; height: 200px" fit="fill" :src="item.imageUrl" :preview-src-list="srcList"></el-image>
                          <div class="imgNameBox">
                            <el-checkbox @change="checkChange(item)" v-model="item.checked">{{item.imageName}}</el-checkbox>
                          </div>
                          <div class="flex-row-between">
                            <el-button type="text" @click="imgName(item)">更名</el-button>
                            <el-button type="text" @click="showCopy(item.imageUrl)">链接</el-button>
                            <el-button type="text" @click="showChangeClass(item)">分组</el-button>
                            <el-button type="text" @click="removeImgToBack(item)">删除</el-button>
                          </div>
                        </div>
                      </li>
                    </ul>
                  </div>
                  <div class="fy flex-row-between">
                    <div>
                      <el-button @click="allRemove">批量删除</el-button>
                      <el-button type="primary" @click="allChangeClass">批量分组</el-button>
                    </div>
                    <div>
                      <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :page-sizes="[10, 20, 30]"
                        :page-size="getImgsForm.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="imgsTotal">
                      </el-pagination>
                    </div>
                  </div>
                </div>
                <div v-else>暂无图片，可以点击左上角“上传图片”按钮添加</div>
              </el-card>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <!-- 添加分组弹出框 -->
    <el-dialog title="添加分组" :visible.sync="insertGalleryShow" width="30%">
      <el-form :model="insertGalleryForm" ref="insertGalleryForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="分组名称">
          <el-input v-model="insertGalleryForm.galleryName" maxlength="6" show-word-limit @input="change($event)"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="insertGalleryShow = false">取 消</el-button>
        <el-button :loading="insertGalleryLoading" type="primary" @click="insertGallery('insertGalleryForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改分组名称 -->
    <el-dialog title="编辑分组" :visible.sync="editGalleryShow" width="30%">
      <el-form :model="editGalleryForm" ref="editGalleryForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="分组名称">
          <el-input v-model="editGalleryForm.galleryName" maxlength="6" show-word-limit  @input="change($event)"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editGalleryShow = false">取 消</el-button>
        <el-button :loading="editGalleryLoading" type="primary" @click="editGallery">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 上传图片 -->
    <el-dialog title="上传图片" :visible.sync="uploadImg" :before-close="uploadImgClose" width="70%">
      <el-form :model="uploadImgForm" ref="editGalleryForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="本地图片">
          <el-upload ref="upload" :action="action" name="files" multiple list-type="picture-card" :on-success="imgUp" :on-remove="imgRemove" :on-exceed="handleExceed" :limit="20" :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">单次最多上传20张图片，仅支持 gif、 jpeg、 png、 bmp 4种格式, 大小不超过3.0 MB</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="选择分组">
          <el-select v-model="uploadImgForm.galleryId" placeholder="请选择">
            <el-option v-for="item in updateClassArr" :key="item.name" :label="item.galleryName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="text-align:center;">
          <el-button :loading="uploadImgLoading" type="primary" @click="uploadImgFun">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 图片更改名称 -->
    <el-dialog title="编辑图片名称" :visible.sync="imgNameShow" width="30%">
      <el-form :model="imgNameForm" ref="imgNameForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="编辑名称">
          <el-input v-model="imgNameForm.imageName" @input="change($event)" maxlength="12" show-word-limit></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="imgNameShow = false">取 消</el-button>
        <el-button :loading="imgNameLoading" type="primary" @click="changeImgName('imgNameForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 复制链接 -->
    <el-dialog title="复制链接" :visible.sync="copyShow" width="30%">
      <el-input v-model="copyUrl" :disabled="true"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="copyShow = false">取 消</el-button>
        <el-button class="cobyOrderSn" :data-clipboard-text="copyUrl" @click="copyLink">复制</el-button>
      </span>
    </el-dialog>
    <!-- 分组 -->
    <el-dialog title="分组" :visible.sync="changeClassShow" width="30%">
      <div>
        <el-select @change="change($event)" v-model="changeClassForm.id" placeholder="请选择分组">
          <el-option v-for="(item,index) in changeClassArr" :key="index" :label="item.galleryName" :value="item.id"></el-option>
        </el-select>
      </div>
      <span slot="footer" class="dialog-footer"> 
        <el-button @click="changeClassShow = false">取 消</el-button>
        <el-button :loading="changeClassLoading" type="primary" @click="subChangeClass">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除分组 -->
    <el-dialog title="删除分组" :visible.sync="deleteClassShow" width="30%">
      <el-form :model="deleteClassForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="确定删除">
          <div>{{'【'+deleteClassForm.galleryName+'】'}}</div>
        </el-form-item>
        <el-form-item label="图片转移至">
          <el-select @change="change($event)" v-model="deleteClassForm.galleryId" placeholder="请选择分组">
            <el-option v-for="(item,index) in deleteCLassArr" :key="index" :label="item.galleryName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer"> 
        <el-button @click="deleteClassShow = false">取 消</el-button>
        <el-button :loading="deleteClassLoading" type="primary" @click="subDeleteClass">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getGalleryByShopId ,
  insertGallery ,
  updateGalleryName ,
  deleteGallery ,
  getGalleryImageByGroup ,
  deleteOssFile ,
  addGalleryImage ,
  updateGalleryImageName ,
  updateGalleryImageGroup ,
  deleteGalleryImage ,
} from '@/api/shop/shopMaterial'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue) this.getGaller()
    }
  },
  data(){
    return{
      rules: {
        galleryName: [
          { required: true, message: '请输入分组名称', trigger: 'blur' },
          { min: 1, max: 6, message: '长度在 1 到 6 个字符', trigger: 'blur' }
        ],
      },
      rules2: {
        galleryName: [
          { required: true, message: '请输入分组名称', trigger: 'blur' },
          { min: 1, max: 6, message: '长度在 1 到 6 个字符', trigger: 'blur' }
        ],
      },
      imageClassTags:[],                  // 分组按钮
      getGalleryForm:{},                  // 获取分组数据
      insertGalleryShow:false,            // 添加分组弹出框
      insertGalleryLoading:false,         // 添加分组按钮loading
      insertGalleryForm:{},               // 添加分组数据
      editGalleryShow:false,              // 编辑分组弹出框
      editGalleryForm:{},                 // 编辑分组数据
      editGalleryLoading:false,           // 编辑按钮loading

      getImgsForm:{                       // 分组下的图片数据
        pageSize:30,
        pageNum:1
      },
      imgs:[],
      imgsTotal:0,
      allChecked:false,      // 全选
      selectImgs:[],
      uploadImg:false,                    // 上传图片弹出框
      uploadImgLoading:false,             // 上传图片loading
      uploadImgForm:{                     // 上传图片数据
        galleryId:0
      },                   
      galleryList:[],                     // 准备上传的图片数据
      activeClassName:'',                 // 默认选中的分组名称

      imgNameShow:false,                  // 图片更改名称弹出框
      imgNameForm:{},                     // 图片改名数据
      imgNameLoading:false,               // 图片改名loading

      copyShow:false,
      copyUrl:'',

      deleteClassShow:false,              // 删除分组弹出框
      deleteClassLoading:false,           // 删除分组loading
      deleteClassForm:{
        galleryId:0
      },                 // 
      deleteCLassArr:[],                  // 删除分组后的当前分组

      changeClassShow:false,
      changeClassArr:[],                  // 批量分组的数据
      changeClassForm:{},                 // 批量分组的数据
      changeClassLoading:false,           // 批量分组loading
      updateClassArr:[],
      ImageBoxHeight:0,             // 默认数据 - 图片区域高度
      OperationHeight:0,            // 默认数据 - 操作区域高度
      srcList: [],
      action:''                     // 默认数据 - 上传图片地址
    }
  },
  created(){
    // 分组默认数据
    this.getGalleryForm.shopId = this.shop.shopId
    this.getGalleryForm.type = 1
    // 分组下的图片默认数据
    this.getImgsForm.shopId = this.shop.shopId
    this.getImgsForm.type = 1
    this.action = `/product/business/gallery/uploadGalleryImage?shopId=${this.shop.shopId}`
    this.getGaller()
  },
  mounted() {
    this.$nextTick(function() {
      this.OperationHeight = window.innerHeight - 340;
      this.ImageBoxHeight = window.innerHeight - 434;
    })
    window.onresize = () => {
      return (() => {
        this.OperationHeight = window.innerHeight - 340;
        this.ImageBoxHeight = window.innerHeight - 434;
      })();
    };
  },
  methods:{
    // 图片 - 获取分组下图片列表
    getImgs(){
      getGalleryImageByGroup(this.getImgsForm).then(res=>{
        console.log('获取分类下图片',res)
        let data = res.data.list;
        data.forEach(e=>e.checked = false)
        this.imgs = data;
        this.imgsTotal = res.data.total;
      })
    },
    // 图片 - 点击图片放大
    showBigImg(e){
      this.srcList = [];
      this.srcList.push(e)
    },
    // 图片 - 改名
    imgName(e){
      console.log(e)
      this.imgNameForm.imageName = e.imageName
      this.imgNameForm.id = e.id
      this.imgNameShow = true
    },
    // 图片 - 改名
    changeImgName(){
      if(this.imgNameForm.imageName == ''){
        this.$message.error('请填写图片名称');
        return;
      }
      this.imgNameLoading = true;
        updateGalleryImageName(this.imgNameForm).then(res=>{
          console.log('更改图片名称',res)
          if(res.code == 200){
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.getImgs();
            this.imgNameShow = false;
            setTimeout(()=>{
              this.imgNameLoading = false;
            },1000)
          }else{
            this.imgNameLoading = false;
            this.$message.error(res.message);
          }
        })
    },
    // 图片 - 批量改
    allChangeClass(){
      console.log(this.selectImgs)
      if(this.selectImgs.length == 0){
        this.$message.error('请至少选择一张图片再试');
        return;
      }
      let ids = []
      this.selectImgs.forEach(e=>{
        ids.push(e.id)
      })
      this.changeClassForm.ids = ids.join(",")
      this.changeClassForm.id = this.selectImgs[0].galleryId
      this.changeClassShow = true
    },
    // 图片 - 改分组
    showChangeClass(e){
      console.log(e)
      this.changeClassForm.ids = e.id;
      this.changeClassForm.id = e.galleryId
      this.changeClassShow = true
    },
    // 提交分组
    subChangeClass(){
      this.changeClassLoading = true;
      let obj = {};
      obj.id = this.changeClassForm.ids;
      obj.galleryId = this.changeClassForm.id;
      updateGalleryImageGroup(obj).then(res=>{
        console.log('改分组',res)
        if(res.code == 200){
          this.$message({
            message: '分组成功',
            type: 'success'
          });
          this.getGaller()
          this.selectImgs = []
          this.allChecked = false;
          this.changeClassShow = false;
          setTimeout(()=>{
            this.changeClassLoading = false;
          },1000)
        }else{
          this.changeClassLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 图片 - 批量移动到回收站
    allRemove(){
      if(this.selectImgs.length == 0){
        this.$message.error('请至少选择一张图片再试');
        return;
      }
      let id = []
      this.selectImgs.forEach(e=>{
        id.push(e.id)
      })
      id = id.join(",")
      let obj = {};
      obj.id = id;
      this.removeFun(obj);
    },
    // 图片 - 移动到回收站
    removeImgToBack(e){
      console.log(e)
      let obj = {}
      obj.id = e.id
      this.removeFun(obj);
    },
    removeFun(e){
      this.$confirm('若删除，置入回收站，不会对目前已使用该图片的相关业务造成影响。', '确定删除该图片？', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteGalleryImage(e).then(res=>{
          console.log('移动到回收站',res)
          if(res.code == 200){
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.allChecked = false;
            this.selectImgs = []
            this.getGaller()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 图片 - 复制框
    showCopy(e){
      this.copyUrl = e;
      this.copyShow = true;
    },
    // 图片 - 复制链接
    copyLink() {
      let clipboard = new this.clipboard('.cobyOrderSn');
      console.log(clipboard)
      clipboard.on('success', () => {
        this.$message({
          message: '复制成功',
          type: 'success'
        });
      });
      clipboard.on('error', () => {
        this.$message.error('复制失败');
      });
    },
    // 图片 - 勾选
    checkChange(e){
      console.log('删除前的数组',this.selectImgs)
      if(e.checked){
        this.selectImgs.push(e)
      }else{
        console.log('2')
        this.selectImgs.splice(this.selectImgs.findIndex(item => item.id === e.id), 1)
      }
      this.allChecked = this.selectImgs.length == this.imgsTotal
      console.log('删除后的数组',this.selectImgs)
    },
    // 图片 - 全选
    allSelect(e){
      if(e){
        console.log('点了全选')
        this.imgs.forEach(e=>{
          e.checked = true
          this.selectImgs.push(e)
        })
      }
      if(!e){
        console.log('取消了全选')
        this.imgs.forEach(e=>{e.checked = false})
        this.selectImgs = []
      }
    },
    // 图片 - 上传
    imgUp(file, fileList){
      console.log(file, fileList);
      let obj = {};
      obj.imageName = file.data[0].orginName
      obj.imageSize = file.data[0].size
      obj.imageUrl = file.data[0].name
      obj.width = file.data[0].width
      obj.height = file.data[0].height
      this.galleryList.push(obj)
    },
    // 图片 - 删除
    imgRemove(file, fileList){
      console.log('图片删除',file, fileList);
      if(file.response){
        let obj = {};
        let galleryList = []
        let obj1 = {};
        obj1.imageUrl = file.response.data[0].name;
        galleryList.push(obj1)
        obj.shopId = this.shop.shopId;
        obj.type = 1;
        obj.galleryList = galleryList;
        this.deleteImgFun(obj)
        this.galleryList = [];
        fileList.forEach(e=>{
          let gObj = {};
          gObj.imageName = e.response.data[0].orginName
          gObj.imageSize = e.response.data[0].size
          gObj.imageUrl = e.response.data[0].name
          gObj.width = e.response.data[0].width
          gObj.height = e.response.data[0].height
          this.galleryList.push(gObj)
        })
      }
    },
    // 图片删除 - 请求方法
    deleteImgFun(e){
      deleteOssFile(e).then(res=>{
        console.log('删除oss资源',res)
      })
    },
    // 图片 上传请求
    uploadImgFun(){
      this.uploadImgForm.type = 1 
      this.uploadImgForm.shopId = this.shop.shopId 
      if(this.galleryList.length == 0){
        this.$message.error('请先上传图片');
        return;
      }
      this.uploadImgLoading = true;
      this.uploadImgForm.galleryList = this.galleryList;
      addGalleryImage(this.uploadImgForm).then(res=>{
        console.log('上传图片保存',res)
        if(res.code == 200){
          this.$message({
            message: '恭喜，上传成功',
            type: 'success'
          });
          this.getGaller();
          this.uploadImgForm.galleryId = 0
          this.galleryList = []
          this.$refs['upload'].clearFiles()
          this.uploadImg = false;
          setTimeout(()=>{
            this.uploadImgLoading = false;
          },1000)
        }else{
          this.uploadImgLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 图片 - 弹出框关闭
    uploadImgClose(done){
      this.$confirm('确认关闭？').then(_ => {
        console.log('执行了',this.galleryList)
        if(this.galleryList.length > 0){
          let obj1 = {};
          let galleryList = []
          this.galleryList.forEach(e=>{
            let obj = {};
            obj.imageUrl = e.imageUrl
            galleryList.push(obj)
          })
          obj1.shopId = this.shop.shopId;
          obj1.type = 1;
          obj1.galleryList = galleryList;
          this.galleryList = []
          this.$refs.upload.clearFiles()
          this.deleteImgFun(obj1)
        }
        done();
      }).catch(_ => {});
    },
    // 图片 - 数量限制
    handleExceed(files, fileList) {
      this.$message.warning(`单次最多上传20张图片，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    // 图片 - 格式检测
    beforeAvatarUpload(file) {
      console.log('file',file)
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/gif' || file.type === 'image/png' || file.type === 'image/bmp';
      const isLt3M = file.size / 1024 / 1024 < 3;
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt3M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt3M;
    },
    // 分类 - 获取图片库分类
    getGaller(){
      console.log('请求分类的数据',this.getGalleryForm)
      getGalleryByShopId(this.getGalleryForm).then(res=>{
        console.log('获取图片库分组',res)
        let data = res.data;
        data.forEach(e=>{ e.type = 'info' })
        if(this.activeClassName == ''){
          this.activeClassName = data[0].galleryName
          data[0].type = ''
          this.getImgsForm.galleryId = data[0].id;
        }else{
          let index = data.findIndex(item => item.galleryName == this.activeClassName);
          console.log('index',index)
          data[index].type = ''
          this.getImgsForm.galleryId = data[index].id;
        }
        let arr = []
        for(var i=1;i<data.length;i++){
          arr.push(data[i])
        }
        this.updateClassArr = arr;
        this.changeClassArr = arr;
        this.deleteCLassArr = arr;
        console.log('arr',arr)
        this.getImgs()
        this.imageClassTags = data
      })
    },
    // 分类 - 添加分类
    insertGallery(){
      if(!this.insertGalleryForm.galleryName){
        this.$message.error('请输入分组名称');
        return;
      }
      this.insertGalleryForm.shopId = this.shop.shopId
      this.insertGalleryForm.type = 1
      this.insertGalleryLoading = true;
      console.log('请求添加分组的数据',this.insertGalleryForm)
      insertGallery(this.insertGalleryForm).then(res=>{
        console.log('添加分组',res)
        if(res.code == 200){
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.insertGalleryShow = false;
          this.insertGalleryForm.galleryName = ''
          this.getGaller('',1)
          setTimeout(()=>{
            this.insertGalleryLoading = false;
          },1000)
        }else{
          this.insertGalleryLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 分类 - 编辑当前组
    editClass(e){
      console.log('点击了编辑按钮')
      console.log(e)
      this.editGalleryForm.galleryName = e.galleryName
      this.editGalleryForm.id = e.id
      this.editGalleryShow = true;
    },
    // 分类 - 选择当前组
    activeClass(e){
      console.log('点击了分组按钮')
      this.imageClassTags.forEach(el => el.type = 'info')
      e.type  = ''
      this.selectImgs = [];
      this.allChecked = false;
      this.getImgsForm.galleryId = e.id
      this.activeClassName = e.galleryName;
      this.getImgsForm.imageName = ''
      this.getImgs()
    },
    // 分类 - 删除当前组
    deleteClass(e){
      console.log('点击了删除按钮',e)
      this.deleteClassShow = true;
      this.deleteClassForm.galleryName = e.galleryName
      this.deleteClassForm.id = e.id
      // deleteCLassArr
      this.deleteCLassArr.splice(this.deleteCLassArr.findIndex(item => item.id === e.id), 1)
    },
    subDeleteClass(){
      this.deleteClassForm.galleryId = this.deleteClassForm.galleryId ? this.deleteClassForm.galleryId : 0
      console.log(this.deleteClassForm)
      this.deleteClassLoading = true;
      deleteGallery(this.deleteClassForm).then(res=>{
        console.log('删除分组',res)
        if(res.code == 200){
          this.$message({
            message: '删除分组成功',
            type: 'success'
          });
          if(this.deleteClassForm.galleryName == this.activeClassName) this.activeClassName = ''
          this.getGaller();
          this.deleteClassShow = false;
          setTimeout(()=>{
            this.deleteClassLoading = false;
          },1000)
        }else{
          this.deleteClassLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 分类 - 修改分类名称
    editGallery(){
      if(!this.editGalleryForm.galleryName){
        this.$message.error('请输入分组名称');
        return;
      }
      this.editGalleryForm.shopId = this.shop.shopId
      this.editGalleryForm.type = 1
      this.editGalleryLoading = true;
      console.log('请求分组名称的数据',this.editGalleryLoading)
      updateGalleryName(this.editGalleryForm).then(res=>{
        console.log('修改分组',res)
        if(res.code == 200){
          this.$message({
            message: '编辑成功',
            type: 'success'
          });
          this.editGalleryShow = false;
          this.activeClassName = this.editGalleryForm.galleryName
          this.getGaller('',1)
          setTimeout(()=>{
            this.editGalleryLoading = false;
          },1000)
        }else{
          this.editGalleryLoading = false;
          this.$message.error(res.message);
        }
      })
    },

    // 图片 - 分页
    handleSizeChange(val) {
      this.getImgsForm.pageSize = val;
      this.getImgs()
    },
    // 图片 - 分页
    handleCurrentChange(val) {
      this.getImgsForm.pageNum = val;
      this.getImgs()
    },
    // 默认操作方法
    // 文本框输入
    change(e){
      this.$forceUpdate()
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
